@mixin retina {
  @media (max-resolution: $retina-dpi) {
    @content;
  }
}

// Desktop first

@mixin vp-1365 {
  @media (max-width: $vp-1366 - 1) {
    @content;
  }
}

@mixin vp-767 {
  @media (max-width: $vp-768 - 1) {
    @content;
  }
}

@mixin vp-319 {
  @media (max-width: $vp-320 - 1) {
    @content;
  }
}

